import React from 'react'
import { Grid } from '../../../../../components/common/Grid/Grid'
import RankImage from '../../../../../components/common/Rank/RankImage'
import ProfileImage from '../../../../../components/UI/Atoms/Profile/ProfileImage'
import { useDebug } from '../../../../../hooks/basic/useDebug'
import { dayList } from '../../../../../utils/basic/getDate'
import ClearIcon from '@mui/icons-material/Clear';
import PanoramaFishEyeIcon from '@mui/icons-material/PanoramaFishEye';
import { getFullname } from '../../../../../utils/render/getFullname'

const AttendanceList = ({ data, year, month }) => {

  const Day = ({day, dayW, sun, sat}) => {
    if (sun) {
      return(
        <div className='flex flex-col'>
          <span className='text-[#f20707] opacity-60'>{day}</span>
          <span className='text-[#f20707] opacity-60'>{dayW}</span>
        </div>
      )
    }
    if (sat) {
      return(
        <div className='flex flex-col'>
          <span className='text-[#1E6FED] opacity-60'>{day}</span>
          <span className='text-[#1E6FED] opacity-60'>{dayW}</span>
        </div>
      )
    }
    return(
      <div className='flex flex-col'>
        <span>{day}</span>
        <span>{dayW}</span>
      </div>
    )
  }

  const dayOfWeek = (year, month, day) => {
    let d = new Date(`${year}-${month}-${day}`)
    const date = d.getDay()
    switch (date) {
      case 0 : return <Day day={day} dayW='일' sun/>
      case 1 : return <Day day={day} dayW='월' />
      case 2 : return <Day day={day} dayW='화' />
      case 3 : return <Day day={day} dayW='수' />
      case 4 : return <Day day={day} dayW='목' />
      case 5 : return <Day day={day} dayW='금' />
      case 6 : return <Day day={day} dayW='토' sat/>
      default: return ''
    }
  }

  // useDebug(data)

  
  return (
    <>
    <Grid full>
      <Grid.Head col>
        <Grid.Col fixed='365px' className='font-bold text-[16px] text-center'>수련생 정보</Grid.Col>
        <Grid.Col fixed='930px' className='p-0'>
          <Grid.Row calendar>
            { React.Children.toArray(
              dayList.map((day) =>
                <Grid.Col calendar>
                  {dayOfWeek(year, month, day)}
                </Grid.Col>
              )
            ) }
          </Grid.Row>
        </Grid.Col>
      </Grid.Head>
      <Grid.RowWrap>
        { data &&
          React.Children.toArray(
            data?.map( item =>
              <Grid.Row>
                <Grid.Col fixed='365px' className='pl-4 pr-1'>
                  <Grid.Row>
                    <Grid.Col fixed='55px' className='p-0'><ProfileImage urlKey={item?.student_info[0]?.photo_url || ''} /></Grid.Col>
                    <Grid.Col fixed='90px' className='p-0'>{getFullname(item?.student_info[0]?.last_name || '', item?.student_info[0]?.first_name || '')}</Grid.Col>
                    <Grid.Col fixed='60px' className='p-0 pt-[6px]'><RankImage src={item?.student_info[0]?.level || ''} /></Grid.Col>
                    <Grid.Col fixed='120px' className='p-0'>{item?.class_data?.title || ''}</Grid.Col>
                  </Grid.Row>
                </Grid.Col>
                <Grid.Col fixed='930px' className='p-0'>
                  <Grid.Row calendar>
                    { React.Children.toArray(
                      dayList.map((day) => 
                        <Grid.Col calendar>
                          { !item?.att_data[day] ? 
                            <span className="text-[#bbbbbb]">·</span> 
                            : 
                            item?.att_data[day]?.is_attended == 1 ? 
                            <span className="text-[#037E00]">
                              <PanoramaFishEyeIcon className='w-[15px] h-[15px] mt-[8px]' />
                            </span> 
                            : 
                            <span className="text-[#FF0000]">
                              <ClearIcon fontSize='small' className='mt-[8px] w-[15px] h-[15px]' />
                            </span> 
                          }
                        </Grid.Col>
                      )
                    )}
                  </Grid.Row>
                </Grid.Col>
              </Grid.Row>
            )
          )
        }
      </Grid.RowWrap>
    </Grid>
    </>
  )
}

export default AttendanceList