import { useQuery } from '@tanstack/react-query'
import React, { useEffect, useState } from 'react'
import { useRecoilValue } from 'recoil'
import { getAttendanceMonth } from '../../../../api/Attendance/attendanceApi'
import ClassSelectMD from '../../../../components/common/Class/ClassSelectMD'
import MonthSelect from '../../../../components/common/Select/MonthSelect'
import YearSelect from '../../../../components/common/Select/YearSelect'
import PageContainer from '../../../../components/layout/PageContainer'
import { useDebug } from '../../../../hooks/basic/useDebug'
import { useSelect } from '../../../../hooks/basic/useSelect'
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom'
import { dayList, getCurrMonth, getCurrYear } from '../../../../utils/basic/getDate'
import AttendanceList from './AttendanceList/AttendanceList'
import ClearIcon from '@mui/icons-material/Clear';
import PanoramaFishEyeIcon from '@mui/icons-material/PanoramaFishEye';

const AttendanceStatusPage = () => {
  const dojangId = useRecoilValue(dojangIdAtom)
  let yy = getCurrYear().toString()
  let mm = getCurrMonth().toString()
  
  // custom hook
  const [ classId, handleChangeClasses ] = useSelect('0')
  const [ year, handleYear ] = useSelect(yy)
  const [ month, handleMonth ] = useSelect(mm)

  // query
  const { data, isFetching } = useQuery(['getAttendanceMonth', dojangId, classId, year, month], () => getAttendanceMonth(dojangId, classId, year, month), {
    enabled: !!dojangId && !!classId && !!year && !!month,
    select: (res) => {
      let temp = res.map((v) => {
        const dayList = new Array(31)
        v.att_data?.forEach(item => {
            let dayIdx = Number(item?.createdAt.slice(-2))
            dayList[dayIdx] = item
          }
        )
        let vv = {...v, att_data: dayList}
        return vv
      })
      return temp
    }
  })

  
  return (
    <>
    <PageContainer width="lg">
      <div className='flex justify-between items-center mb-3 w-full'>
        <div className="flex gap-[5px]">
          <ClassSelectMD value={classId} onChange={handleChangeClasses} />
          <YearSelect value={year} onChange={handleYear} />
          <MonthSelect value={month} onChange={handleMonth} />
        </div>
        <div className="flex gap-[10px] pt-[15px]">
          <span className="text-[#037E00] flex items-center gap-[5px]"><PanoramaFishEyeIcon fontSize='small' className='text-[#037E00]' />출석</span>
          <span className="text-[#FF0000] flex items-center gap-[1px]"><ClearIcon fontSize='small' className='text-[#FF0000]' />결석</span>
        </div>
      </div>
      <AttendanceList data={data} year={year} month={month}/>

    </PageContainer>
    </>
  )
}

export default AttendanceStatusPage